import React, { Component } from 'react'
import { Form, message, Upload, Icon } from 'antd'

const FormItem = Form.Item;
const Dragger = Upload.Dragger;

class UploadFile extends Component {

  onChange = (info) => {
    const { status } = info.file
    const { onUploadFile } = this.props

    if (status === 'uploading') onUploadFile({spinning: true}) 
    if (status === 'done') {
      this.onSuccess(info)
    } else if (status === 'error') {
      message.error(`${info.file.name} `);
      onUploadFile({spinning: false})
    }
  }

  onSuccess(info) {
    const { onUploadFile } = this.props
    const { code, data } = info.file.response
    const msg = info.file.response.message || ''

    if (code === 200) {
      message.success(`${info.file.name} `);
      onUploadFile({spinning: false, ...data})
    } else {
      message.error(msg)
      onUploadFile({spinning: false})
    }
  }

  render() {

    const { loading } = this.props
    const uploadConfig = {
      accept: '.jar, .zip, .tar',
      disabled: loading,
      withCredentials: true,
      showUploadList: { showRemoveIcon: false },
      action: '/api/file/upload',
      onChange: this.onChange.bind(this)
    };

    return (
      <FormItem
        colon={false}
        label=""
      >
        <Dragger {...uploadConfig}>
          <p className="ant-upload-drag-icon">
            <Icon type="inbox" />
          </p>
          <p className="ant-upload-text"></p>
          <p className="ant-upload-hint">jarziptar</p>
        </Dragger>
      </FormItem>
    )
  }
}

export default UploadFile


